| Copyright (c) Jupyter Development Team.
| Distributed under the terms of the Modified BSD License.
|----------------------------------------------------------------------------*/

.elyra-browseFileDialog .jp-Dialog-content {
  height: 400px;
  width: 600px;
}

.launcher-body {
  padding: 20px;
  width:80%;
  margin: 0 auto;
  /* Center horizontally */
}

.launcher-content {
  max-width: 1200px;
  margin: auto;
  width: 100%;
}

.launcher-title {
  margin-top: 32px;
  font-size: 36px;
  font-weight: bold;
  color: #1a202c;
  display: flex;
  align-items: center;
}

.launcher-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 28px;
}

.launcher-card {
  background-color: white;
  padding: 16px;
  border-radius: 4px;
  /* border: 1px solid #e2e8f0; */
}

.launcher-card-header h3 {
  font-size: 24px;
  font-weight: bold;
  color: #1a202c;
}

.launcher-card-links {
  display: flex;
  flex-wrap: wrap;
  margin: -4px;
}

.launcher-card-links li {
  padding: 4px;
  margin-bottom: 20px;
}

.launcher-card-links a {
  font-size: 12px;
  font-weight: 500;
  color: #3182ce;
  text-decoration: none;
}

.launcher-card-list {
  margin-top: 16px;
  list-style: none;
  padding: 0;
}

.launcher-card-list li {
  margin-bottom: 20px;
}

.launcher-card-item {
  display: flex;
  align-items: center;
  padding: 16px;
  border-radius: 4px;
  border: 1px solid #F2F4F7;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s;
}

.launcher-card-item:hover {
  border-color: #a0aec0;
  background-color: #f7fafc;
}

.launcher-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  border-radius: 50%;
}

.alert-box {
  margin-top: 20px;
  padding: 16px;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
  background-color: white;
}

.alert-content {
  display: flex;
  align-items: start;
  gap: 16px;
}

.alert-icon {
  color: #4a5568;
}

.alert-text h2 {
  font-size: 20px;
  font-weight: bold;
  color: #1a202c;
}

.alert-text p {
  margin-top: 8px;
  font-size: 14px;
  color: #4a5568;
}

.alert-close-btn {
  color: #a0aec0;
  transition: color 0.3s;
  background: none;
  border: none;
  cursor: pointer;
}

.alert-close-btn:hover {
  color: #718096;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
